
.container{
    display: flex;
    justify-content: space-between;/*Essa propriedade alinha eles horizontalmente, sem precisar ficar empurrando da margem esquerda*/
    
}

.box1{
    /*Essas três propriedades alinhas o texto no centro*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 2px groove rgb(101, 25, 252);
    border-radius: 10px;
    max-height:fit-content;
    width: 400px;
    margin-top: 150px;
    margin-left: 30px;
    
}
